.page {
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
}

.page__body {
  margin: 0;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding-inline: 16px;
  display: flex;
}

.product-card__image {
  object-position: center;
  margin-inline: 3px;
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.product-card__product-title {
  text-align: left;
  color: #060b35;
  height: 36px;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__product-code {
  color: #616070;
  align-self: start;
  width: 98px;
  height: 14px;
  margin-top: 4px;
  margin-bottom: 0;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.product-card__price-name {
  color: #616070;
  width: 31px;
  height: 18px;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.product-card__price-value {
  width: 50px;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__bottom-buy {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
}

.product-card__bottom-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  line-height: 38px;
}

.reviews {
  color: #060b35;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
